
import api from '../api'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

export default {
  name: 'panel-cart',
  data: function() {
    return {
      quantity: '数量',
      init:true,
    }
  },
  props: {
    item: Object,
    index:Number
  },
  created: function() {
    console.log(this.item);
  },
  methods:{
    update_cart:function(val){
      var self=this;
      console.log(val);
      console.log("}}}}}}}}}}");
      if(self.item.cart_objects.quantity==val) return;
      if(val==0) return;
     
      api.update_cart({
        quantity:val,
        goods_id:self.item.cart_objects.goods_id,
        id:self.item.cart_objects.id
      }).then(res=>{
        console.log(res);
        if(res.ok){
          if(res.status){
            //什么也不做
          }else{
            self.$vux.toast.show({
              text:'<span class="font-normal">'+res.data.msg+'</span>',
              type:'warn'
            })
          }
        } else{
          self.$vux.toast.show({
              text:'<span class="font-normal">'+res.statusText+'</span>',
              type:'warn'
          })
        }
      })
    }
  },
  components: {
    Flexbox,
    FlexboxItem,
    XNumber
  }
}



