
import PanelCart from './panel-cart.vue'
import api from '../api/index.js'
import * as config from '../config/config.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
  name: 'cart',
  data: function() {
    return {
      type: '1',
      cart_data: [],
    }
  },
  created: function() {
    this.fetch_data();
    //拉取数据
  },
  methods: {
    handleEvents: function() {

    },
    handle_delete: function(index) {
      console.log(index);
      var self = this;
      //执行确认操作
      self.$vux.confirm.show({
        title: '提示',
        content: '确认从购物车删除?',
        onCancel: function() {
          self.$vux.confirm.hide();
        },
        onConfirm: function() {
          console.log("确认删除");
          self.$vux.confirm.hide();
          self.del_cart(index);
        }
      })
    },
    del_cart: function(index) {
      var self = this;
      api.del_cart({
        id: self.cart_data[index].cart_objects.id
      }).then(res => {
        console.log(res);
        if (res.ok) {
          if (res.data.status) {
            self.$vux.toast.show({
              text: '<span class="font-normal">' + res.data.msg + '</span>',
              type: 'text'
            })
            self.fetch_data();
          } else {
            self.$vux.toast.show({
              text: '<span class="font-normal">' + res.data.msg + '</span>',
              type: 'warn'
            })
          }
        } else {
          self.$vux.toast.show({
            text: '<span class="font-normal">' + res.statusText + '</span>',
            type: 'warn'
          })
        }
      })
    },
    fetch_data: function() {
      var self = this;
      api.get_cart_data().then(res => {
        console.log(res);
        if (res.ok) {
          if (res.data.data.length) {
            self.cart_data = res.data.data;
          } else {
            self.cart_data = res.data.data;
            self.$vux.toast.show({
              text: '<span class="font-normal">购物车为空</span>',
              type: 'text'
            });
          }
        } else {

        }
      })
    },
    submit_order: function() {
      console.log("提交订单需求");
      this.$router.push("order_confirm");
    }
  },
  components: {
    PanelCart,
    Flexbox,
    FlexboxItem,
    XButton,
    Swipeout,
    SwipeoutItem,
    SwipeoutButton,
    Sticky,
    Tabbar,
    TabbarItem
  }
}



