
import api from '../api'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
  name:'user',
  data:function(){
    return {
      loading_status:false,
      username:'',
      email:'',
      user_avatar_bg:'/static/slice/a1.jpg',
      avatar:'/static/slice/user_logo.jpg',
      order_list_data:[
        {
          title:'待付款',
          url:"/order",
          icon:'&#xe61a;',
          show_badge:true,
        },{
          title:'待发货',
          url:"/order",
          icon:'&#xe632;',
          show_badge:true,
        },{
          title:'待收货',
          url:"/order",
          icon:'&#xe66f;',
          show_badge:true,
        },{
          title:'已收货',
          url:"/order",
          icon:'&#xe619;',
          show_badge:true,
        },{
          title:'售后',
          url:"/order",
          icon:'&#xe617;',
          show_badge:true,
        }
      ],
      others_data:[
        {
          title:'地址管理',
          icon:"&#xe620;",
          url:'/address',
          show_badge:false
        },
        {
          title:'我的收益',
          icon:"&#xe612;",
          url:'/wallet',
          show_badge:false
        },
        // {
        //   title:'我的卡券',
        //   icon:"&#xe62f;",
        //   url:'/user',
        //   show_badge:false
        // },
        {
          title:'二维码',
          icon:"&#xe614;",
          url:'/qrcode',
          show_badge:false
        },
        // {
        //   title:'个人设置',
        //   icon:"&#xe61d;",
        //   url:'/setting',
        //   show_badge:false
        // },
        {
          title:'报表中心',
          icon:"&#xe61f;",
          url:'/vchart',
          show_badge:false
        },
        // {
        //   title:'商品收藏',
        //   icon:"&#xe61e;",
        //   url:'/favgoods',
        //   show_badge:false
        // },
        {
          title:'帮助',
          icon:"&#xe622;",
          url:'/service',
          show_badge:false
        }
      ]
    }
  },
  created:function(){
    //先执行判断,拉取新数据
    this.check_locl_token();
    //有本地数据,先使用本地数据,
    this.render_user_info();
  },
  methods:{
    render_user_info:function(){
      var user_info=this.get_user_info();
      if(user_info){
        this.username=user_info.username;
        this.email=user_info.email||'';
        this.avatar='/uploads/'+user_info.avatar||'/static/slice/user_logo.jpg';
      }
    },
    check_locl_token:function(){
       var self=this;
       var result=this.check_token();
       console.log(result);
       if(result==1){
         //未登录用户---不做任何操作
       }else if(result==2){
          //token过期用户
          self.refresh_token(function(){
             console.log("回调执行");
             self.fetch_user_info();
          });
       }else if(result==3){
          //正常状态
          self.fetch_user_info();
          this.loading_status=true;
       }
    },
    fetch_user_info:function(){
          //拉取用户信息
          var self=this;
          console.log("拉取用户信息");
          api.get_user_info().then(res=>{
               console.log(res.data);
               if(res.data.id){
                  self.loading_status=true;
                  self.name=res.data.name;
                  self.username=res.data.username;
                  self.avatar='/uploads/'+res.data.avatar;
                  self.email=res.data.email||'';
                  window.sessionStorage.user_info=JSON.stringify(res.data);
                  console.log("+++++++");
                  console.log(self.loading_status);
               }
          })
    }
  },
  components:{
    Card,
    Flexbox,
    FlexboxItem
  }
}



