
import * as config from '../config/config.js'
import api from '../api'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  name: 'login',
  data: function() {
    return {
      logo_image: config.app_config.logo_image,
      username: '',
      password: '',
      login_msg: "",
    }
  },
  methods: {
    submit_login: function() {
      var self = this;
      if (this.username == "") {
        this.$vux.toast.show({
          text: '<span class="font-normal">用户名不能为空</span>',
          type: "warn"
        })
        return;
      } else if (this.password == "") {
        this.$vux.toast.show({
          text: '<span class="font-normal">请输入密码</span>',
          type: "warn"
        })
        return;
      };
      api.user_login({
        username: self.username,
        password: self.password
      }).then(res => {
        console.log(res);

        if (res.ok) {
          if (res.data.res) {
            var res_data = res.data;
            self.$vux.toast.show({
              text: '<span class="font-normal">' + res_data.req + '</span>',
              type: 'success'
            });
            self.save_token(res_data.data);
            setTimeout(function() {
              self.$router.push("/user");
            }, 2000)
          } else {
            self.login_msg = res.data.req;
          }

        } else {
          self.login_msg = res.data.req;
        }
      })
    }
  },
  components: {
    Group,
    XInput,
    XButton
  }
}



