
import api from '../api'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import value2name from 'vux/src/filters/value2name.js'

export default {
    name: "order_confirm",
    data: function() {
        return {
            step: 0,
            popup_address: false,
            addressData: ChinaAddressData,
            ship_name: '张三',
            ship_mobile: '1850300XXXX',
            ship_addr: "测试街道XX",
            ship_area: ["广东省", "深圳市", "福田区"], //缓存选中地区
            order_data: [],
            cart_data: [],
            memo: '',
            popup_address: false,
            edit_status: false,
            edit_index: null,
            address_data: {
                name: '',
                mobile: '',
                zip: '',
                tel: '',
                area: [],
                addr: '',
                addr_id: null,
                area_name: '',
            },
            address_list: []
        }
    },
    created: function() {
        //拉取购物车数据
        this.fetch_cart_data();
        //拉取地址信息
        this.fetch_addrs();
    },
    methods: {
        edit_address: function() {
            // console.log("click");
            this.popup_address = !this.popup_address;
        },
        fetch_cart_data: function() {
            var self = this;
            api.get_cart_data().then(res => {
                console.log(res);
                if (res.ok) {
                    if (res.data.data.length) {
                        self.cart_data = res.data.data;
                    } else {
                        self.cart_data = res.data.data;
                        self.$vux.toast.show({
                            text: '<span class="font-normal">购物车为空</span>',
                            type: 'text'
                        });
                    }
                } else {

                }
            })
        },

        submit_order_data: function() {
            var self = this;
            console.log(this.cart_data);
            console.log(this.memo);

            var cart_id = [];
            for (var i = 0; i < this.cart_data.length; i++) {
                cart_id.push(this.cart_data[i].cart_objects.id);
            }
            console.log(cart_id);
            //检查数据完整程度

            if (this.ship_area == "") {
                this.$vux.toast.show({
                    text: '<span class="font-normal">区域不能为空</span>',
                    type: 'warn'
                })
                return;
            }
            if (this.ship_name == "") {
                this.$vux.toast.show({
                    text: '<span class="font-normal">姓名不能为空</span>',
                    type: 'warn'
                })
                return;
            }
            if (this.ship_mobile == "") {
                this.$vux.toast.show({
                    text: '<span class="font-normal">手机号不能为空</span>',
                    type: 'warn'
                })
                return;
            }
            var order_data = {
                ship_area: self.ship_area.join("/"),
                ship_addr: self.address_data.ship_addr,
                ship_name: self.address_data.ship_name,
                ship_mobile: self.address_data.ship_mobile,
                addr_id: 0,
                memo: self.memo,
                //addr_id:self.address_data.ship_area,//暂时不传
                cart_id: JSON.stringify({
                    cart_id: cart_id
                })
            }
            console.log(order_data);

            api.add_order(order_data).then(res => {
                if (res.ok) {
                    if (res.data.length) {
                        this.$vux.toast.show({
                            text: '<span class="font-normal">需求已提交,请等待审核</span>',
                            type: 'success'
                        })
                        setTimeout(function() {
                            self.$router.push("user");
                        }, 2000)

                    } else {
                        this.$vux.toast.show({
                            text: '<span class="font-normal">' + res.data.req + '</span>',
                            type: 'warn'
                        })
                    }
                }
                console.log(res);
            })
        },
        getName: function(value) {
            return value2name(value, ChinaAddressData)
        },
        edit_address: function($index) {
            var self = this;
            console.log("+++++++++++++++++++");
            console.log(this.popup_address);
            if (!this.popup_address) {
                //关闭状态-->打开
                this.edit_status = true;

                this.address_data = this.address_list[$index];
            } else {
                //打开状态->关闭
                this.edit_status = false;
                this.fix_origin_data();
            }
            //切换
            // console.log
            this.popup_address = !this.popup_address;
        },
        fetch_addrs: function() {
            var self = this;
            api.get_addrs().then(res => {
                console.log(res);
                if (res.ok) {
                    if (res.data.constructor == Array) {
                        this.address_list = res.data;
                        for (var i = 0; i < res.data.length; i++) {
                            // this.address_list[i].area = res.data[i].area.split("/");
                            this.address_list[i].code = res.data[i].code.split("/");
                        }
                    }
                } else {
                    self.$vux.toast.show({
                        text: '<span class="font-normal">' + res.statusText + '</span>'
                    })
                }
            })
        },
        add_addr: function() {
            this.popup_address = true;
        },
        cancel_address_edit: function() {
            this.popup_address = false;
        },
        save_address_data: function() {
            var self = this;
            this.popup_address = false;
            //判断当前处于更新状态还是新增状态
            var area = self.getName(self.address_data.code);
            console.log(area);
            if (area != '') area = area.split(" ").join("/");

            //校验数据完整程度
            if (self.address_data.mobile == "") {
                self.$vux.toast.show({
                    text: '<span class="font-normal>手机号不能为空</span>',
                    type: 'warn'
                })
                return;
            }
            if (self.address_data.name == "") {
                self.$vux.toast.show({
                    text: '<span class="font-normal>联系人不能为空</span>',
                    type: 'warn'
                })
                return;
            }
            if (self.address_data.area == "") {
                self.$vux.toast.show({
                    text: '<span class="font-normal>区域不能为空</span>',
                    type: 'warn'
                })
                return;
            }

            // if(addr==""){
            //   self.$vux.toast.show({
            //     text:'<span class="font-normal>详细地址不能为空</span>'
            //   })
            //   return;
            // }
            if (this.edit_status) {
                //update 方法
                self.edit_status = false;
                api.update_addr({
                    area: area,
                    addr: '*暂无*',
                    name: self.address_data.name,
                    mobile: self.address_data.mobile,
                    addr_id: self.address_data.id,
                    code: self.address_data.code.join("/"),
                }).then(res => {
                    console.log(res);

                    if (res.ok) {
                        if (res.data.status) {
                            self.$vux.toast.show({
                                text: '<span class="font-normal">更新成功</span>',
                                type: "success"
                            })
                            self.fetch_addrs();
                        } else {
                            self.$vux.toast.show({
                                text: '<span class="font-normal">' + res.data.msg + '</span>',
                                type: "warn"
                            })
                            self.addredd_data.area = self.temp_area;
                        }
                    } else {
                        self.$vux.toast.show({
                            text: '<span class="font-normal">' + res.statusText + '</span>',
                            type: "warn"
                        })
                        self.addredd_data.area = self.temp_area
                    }
                })
            } else {
                //add 方法
                api.add_addr({
                    area: area,
                    addr: '*暂无*',
                    name: self.address_data.name,
                    mobile: self.address_data.mobile,
                    code: self.address_data.area.join("/")
                }).then(res => {
                    console.log(res);
                    if (res.ok) {
                        if (res.data.status) {
                            self.$vux.toast.show({
                                text: '<span class="font-normal">保存成功</span>',
                                type: 'success'
                            })
                            //重新拉取数据
                            self.fetch_addrs();
                        } else {
                            self.$vux.toast.show({
                                text: '<span class="font-normal">' + res.data.msg + '</span>',
                                type: 'warn'
                            })
                        }
                    } else {
                        self.$vux.toast.show({
                            text: '<span class="font-normal">' + res.statusText + '</span>',
                            type: 'warn'
                        })
                    }
                })
            }

        },
        update_addr: function() {
            var self = this;
        },
        del_addr: function(index) {
            var self = this;
            console.log(index);
            //弹窗确认
            self.$vux.confirm.show({
                title: '确定删除改地址',
                onCancel: function() {

                },
                onConfirm: function() {
                    api.del_addr({
                        addr_id: self.address_list[index].id,
                    }).then(res => {
                        console.log(res);
                        if (res.ok) {
                            if (res.data.code == "200") {
                                self.$vux.toast.show({
                                    text: '<span class="font-normal">删除成功</span>',
                                    type: 'success'
                                })
                                self.fetch_addrs();
                            } else {
                                self.$vux.toast.show({
                                    text: '<span class="font-normal">' + res.data.msg + '</span>',
                                    type: 'warn'
                                })
                            }
                        } else {
                            self.$vux.toast.show({
                                text: '<span class="font-normal">' + res.statusText + '</span>',
                                type: 'warn'
                            })
                        }
                    })
                }
            })

        },
        fix_origin_data: function() {
            $(".vux-popup-mask").removeClass(".vux-popup-show").addClass(".vux-popup-show");
        }
    },
    components: {
        Flexbox,
        FlexboxItem,
        Group,
        Cell,
        XButton,
        XTextarea,
        Tabbar,
        TabbarItem,
        XAddress,
        ChinaAddressData,
        Popup,
        XInput,
        Step,
        StepItem
    }
}



